import YPage from '@/components/YPage';
import Vue from 'vue';
// 
const registerComponentList = {
	YPage: YPage
}

// 
export default {
	install() {
		for (let [itemKey, item] of Object.entries(registerComponentList)) {
			// installVue.use;
			if (item.install) {
				Vue.use(item)
			}
			// Vue.component
			else {
				Vue.component(itemKey, item)
			}
		}
	}
}